:root {
  --navigation-color: #171717;
  --navigation-color-transparent: #171717cc;
  --navigation-color-secondary: #da0037;
  --navigation-color-tertiary: #171717;
  --navigation-color-quaternary: #ededed;
  --navigation-color-quaternary-transparent: #edededcc;
  --navigation-color-quinary: aquamarine;
  --navigation-color-septary: #7fffd480;
  --text-color: #e2e2e2;
  --contact-color: #e26f2e;
  --contact-color-transparent: #e26f2eb3;
  --about-color: #3f48cc;
  --about-color-transparent: #3f48ccb3;
  --projects-color: #ea8aba;
  --projects-color-transparent: #ea8abab3;
  --main-color: #193225;
  --main-color-transparent: #193225b3;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  color: var(--text-color);
  background-color: #0000;
  background-image: linear-gradient(#0000001a, #0000001a), url("nature.bb1b4f79.jpg");
  background-position: 0 0, 0 0;
  background-repeat: repeat, repeat;
  background-size: 100% 100%;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  margin: 0;
  padding: 0;
}

.react-root, html, body {
  height: 100%;
  overflow-x: hidden;
}

.glass-box {
  backdrop-filter: blur(15px);
  background: #ffffff0d;
  border: 1px solid #ffffff1a;
  border-radius: 20px;
  padding: 20px 40px;
  box-shadow: 0 5px 15px #0000001a;
}

.glass-box-container {
  width: 99%;
  height: 98%;
  margin: .5%;
  padding: 0;
}

.white-text {
  color: var(--text-color);
  font-size: larger;
}

.transparent-button {
  white-space: nowrap;
  cursor: pointer;
  background-color: #0000;
  border: 0;
  font-weight: bold;
  text-decoration: none;
}

.navigation-bar {
  background-color: var(--navigation-color-transparent);
  z-index: 20;
  border: none;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  align-items: stretch;
  width: 100%;
  height: 6vh;
  padding: 0 0 1vh 1vh;
  display: flex;
  position: absolute;
  top: 0;
}

.navigation-logo {
  flex: 1;
}

.navigation-ul {
  justify-content: space-around;
  align-items: center;
  width: 70%;
  display: flex;
}

.outside-alerter {
  z-index: -1;
  width: 100%;
  height: 100%;
  position: absolute;
}

.fill-container {
  text-align: center;
  width: 100%;
  height: 100%;
  line-height: 5vh;
}

.navigation-container {
  justify-content: center;
  align-items: center;
  width: 20%;
  height: 100%;
  list-style: none;
  display: flex;
}

.user-navigation li:hover *, .navigation-container:hover a {
  color: var(--navigation-color-secondary);
}

.user-navigation {
  background-color: var(--navigation-color-transparent);
  border: solid var(--navigation-color-secondary) 1px;
  flex-direction: column;
  align-items: center;
  width: 20%;
  font-size: x-large;
  display: flex;
  position: absolute;
  top: 6vh;
  right: 1vh;
}

.user-navigation li {
  list-style: none;
}

.burger {
  display: none;
}

.burger div {
  background-color: var(--text-color);
  width: 25px;
  height: 3px;
  margin: 3px;
  transition: all .3s;
}

.burgerToggle .line1 {
  transform: rotate(-45deg)translate(-6.25px, 6.25px);
}

.burgerToggle .line2 {
  opacity: 0;
}

.burgerToggle .line3 {
  transform: rotate(45deg)translate(-6.25px, -6.25px);
}

.login-button {
  width: 60%;
  height: 60%;
}

.forgot-password-span {
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
  position: relative;
}

.general-button {
  color: #111;
  background: var(--text-color);
  border-radius: 4px;
  margin-top: 5px;
  padding: 10px;
  font-size: 1.1em;
  font-weight: 700;
  text-decoration: none;
  display: inline-block;
}

.general-button:hover {
  cursor: pointer;
  background-color: #ffffff80;
}

.button-blocked {
  background: gray;
  border: none;
}

.button-blocked:hover {
  background-color: red;
}

.rgb-button {
  background: var(--navigation-color-transparent);
  border-radius: 50px;
  padding: 1px;
  position: relative;
}

.rgb-button span {
  background-color: var(--navigation-color-tertiary);
  z-index: 1;
  border-radius: 50px;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}

.rgb-button:after {
  content: "";
  opacity: 0;
  filter: blur(4px);
  background: linear-gradient(45deg, #fb0094, #00f, #0f0, #ff0, red, #fb0094, #00f, #0f0, #ff0, red) 0 0 / 400%;
  border-radius: 50px;
  width: 100%;
  height: 100%;
  transition: all .5s;
  animation: 20s linear infinite animate_button;
  position: absolute;
  top: 0;
  left: 0;
}

.rgb-button:hover:after {
  opacity: 1;
}

@keyframes animate_button {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 300% 0;
  }

  100% {
    background-position: 0 0;
  }
}

.colored-background {
  content: "";
  z-index: -1;
  border-radius: 20px;
  width: 110%;
  height: 110%;
  transition: all .5s;
  position: absolute;
  top: -5%;
  left: -5%;
}

.background-glass-box .background-glass-box-child:first-child, .background-glass-box .background-glass-box-child:nth-child(2) {
  content: "";
  z-index: -1;
  border-radius: 8px;
  width: 40%;
  height: 110%;
  transition: all .5s;
  position: absolute;
  top: -5%;
  left: 30%;
  transform: skewX(15deg);
}

.background-glass-box .background-glass-box-child:nth-child(2) {
  filter: blur(30px);
}

.background-glass-box:hover .background-glass-box-child:first-child, .background-glass-box:hover .background-glass-box-child:nth-child(2) {
  border-radius: 20px;
  width: 110%;
  height: 110%;
  top: -5%;
  left: -5%;
  transform: skewX(0);
}

@keyframes animateProjects {
  0%, 100% {
    transform: translateY(50px);
  }

  50% {
    transform: translateY(-50px);
  }
}

.container {
  text-align: justify;
  width: 100%;
  height: 86vh;
  padding: 1% 10% 0%;
  position: relative;
  top: 6vh;
  overflow: hidden auto;
}

.center-container {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.center-container h1 {
  font-size: 100px;
}

.center-container h2 {
  font-size: 40px;
}

.contact-form-submission-text {
  text-align: center;
  margin-top: 20px;
  font-weight: 1000;
}

.declined {
  background: #ff2121b3;
  border: 1px solid #ff2121b3;
}

.loading {
  background: #fff672b3;
  border: 1px solid #fff672b3;
}

.accepted {
  background: #49ff12b3;
  border: 1px solid #49ff12b3;
}

.login-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.login-modal-background {
  background-color: var(--navigation-color-transparent);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.login-modal {
  background-color: var(--navigation-color);
  border-radius: 50px;
  width: 50%;
  height: fit-content;
  padding: 30px;
}

.login-modal-container {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.login-container .login-box {
  background-color: var(--navigation-color-transparent);
  border-radius: 50px;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: fit-content;
  padding: 20px;
  display: flex;
  position: relative;
}

.login-box h2 {
  margin: 10px 0;
}

.alternate-login-options {
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 20%;
  display: flex;
}

.btn-alternate-login {
  z-index: 1;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 40%;
  height: 70px;
  padding: 15px;
  font-size: 18px;
  line-height: 1.2;
  text-decoration: none;
  transition: all .4s;
  display: flex;
  position: relative;
}

.btn-alternate-login:before {
  content: "";
  z-index: -1;
  opacity: 0;
  background: linear-gradient(45deg, #1eff00, #00b7ff);
  border-radius: 10px;
  width: 100%;
  height: 100%;
  transition: all .4s;
  display: block;
  position: absolute;
}

.btn-alternate-login {
  color: var(--navigation-color-tertiary);
  background-color: var(--text-color);
}

.btn-alternate-login img {
  width: 30px;
  margin-right: 15px;
  padding-bottom: 3px;
}

.login-form {
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  text-decoration: none;
  display: flex;
}

.login-form h1 {
  color: var(--text-color);
  margin-top: 10px;
}

.login-form input {
  border: 0;
  border: 2px solid var(--navigation-color-secondary);
  color: var(--text-color);
  text-align: center;
  background: none;
  border-radius: 20px;
  outline: none;
  width: 100%;
  padding: 10px 6px;
  transition: all .25s;
  display: block;
}

.login-form-button {
  width: 80%;
  margin-top: 20px;
}

.create-account, .forgot-password {
  text-align: center;
  color: var(--text-color);
  cursor: pointer;
  background-color: #0000;
  border: #0000;
  width: 100%;
  margin-top: 2%;
  font-size: 16px;
  text-decoration: underline;
}

.password-register-field {
  flex-direction: column;
  align-items: center;
  width: 100%;
  display: flex;
  position: relative;
}

.password-register-span {
  border-radius: 20px;
  justify-content: center;
  width: 100%;
  display: flex;
  position: relative;
}

.password-strength-text {
  z-index: 30;
  justify-content: flex-end;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 60px;
}

.up-arrow {
  border: 10px solid #0000;
  border-right-color: var(--navigation-color);
  position: absolute;
  top: -20px;
  left: 50%;
  transform: rotate(90deg);
}

.password-list-requirement {
  list-style: none;
}

.password-strength-hint-box {
  position: relative;
}

.password-strength-hint {
  background: var(--navigation-color);
  backdrop-filter: blur(100px);
  z-index: 30;
  border-radius: 30px;
  padding: 20px 40px;
  font-size: large;
  position: absolute;
  top: 75px;
  left: 150px;
  box-shadow: 0 22px 70px 4px #0000008f;
}

.btn-alternate-login:hover:before {
  opacity: 1;
}

.hide-password-box {
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 0;
  display: flex;
  position: relative;
}

.hide-password-box-full {
  width: 100%;
}

.hide-password-icon {
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0 8px 0 0;
  display: flex;
  position: absolute;
  right: 0;
}

.hide-password-box img {
  width: 32px;
  height: 32px;
}

.page-bar {
  z-index: 21;
  background-color: var(--about-color);
  width: 100%;
  height: 1vh;
  position: fixed;
  top: 5vh;
}

.bottom-bar {
  background-color: var(--navigation-color-transparent);
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 6vh;
  padding: 0;
  display: flex;
  position: absolute;
  bottom: 0;
}

.bottom-bar ul {
  width: 15%;
  height: 60%;
  transition: all .5s;
  display: flex;
}

.bottom-bar ul li {
  width: 25%;
  height: 100%;
  list-style: none;
}

.bottom-bar ul li a {
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.bottom-bar ul li a img {
  height: 100%;
}

.footer-links:hover {
  filter: invert();
  border-radius: 50px;
  transform: scale(.9);
}

.list-lines {
  text-align: left;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.list-lines h1 {
  margin-top: 20px;
  font-size: 8vh;
}

.list-lines h2 {
  margin-top: 20px;
  font-size: 3vh;
}

.contact-section {
  justify-content: center;
  align-items: center;
  display: flex;
}

.form-container {
  flex-direction: column;
  align-items: center;
  width: 70%;
  height: auto;
  display: flex;
}

.form-container textarea, .form-container span, .form-container input {
  letter-spacing: 1px;
  background: none;
  border: none;
  width: 100%;
  font-size: 16px;
}

.login-form input, .form-container input {
  padding-top: 20px;
  padding-bottom: 20px;
}

.form-container h1 {
  font-size: 60px;
}

.form-container h1, .form-container h2 {
  letter-spacing: 1px;
  text-align: center;
  margin-bottom: 10px;
}

.form-container textarea {
  letter-spacing: 1px;
  resize: none;
  height: 200px;
  display: inline-block;
}

.contact-form-padded {
  width: 100%;
  padding: 20px;
}

.contact-form-submit-div {
  width: 100%;
  margin-top: 20px;
}

.contact-form-submit-div button {
  width: 100%;
  height: 100%;
}

.login-form input::placeholder, .form-container textarea::placeholder, .form-container input::placeholder {
  text-align: center;
  color: var(--navigation-color-quinary);
}

.preferences-container {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.preferences-container h1 {
  font-size: 60px;
}

.preferences-container h2 {
  font-size: 45px;
}

.preferences-container h3 {
  font-size: 30px;
}

.reset-password-full-width {
  text-align: center;
  width: 100%;
  padding: 0;
}

.preferences-yes-no {
  justify-content: space-around;
  width: 100%;
  display: flex;
}

.preferences-separator-line {
  background: linear-gradient(45deg, #0ff, #0f0);
  border: none;
  width: 200%;
  height: 10px;
  margin: 30px;
}

.preferences-yes-no button {
  width: 30%;
}

.container-about {
  padding-top: 5% 20% 5% 20%;
}

.about-information {
  width: 100%;
  margin-top: 5%;
  position: relative;
}

.about-information h2 {
  color: var(--text-color);
  font-size: 4em;
  font-weight: 700;
  line-height: 1.5em;
}

.about-information p {
  text-align: left;
  font-size: 24px;
  font-weight: 400;
}

.dad-joke-container {
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
  margin-bottom: 50px;
  display: flex;
}

.dad-joke-content {
  text-align: justify;
  margin: 20px;
  font-size: 20px;
}

.dad-joke-button {
  color: var(--text-color);
  text-align: center;
  width: 80%;
  height: 40px;
  font-size: 30px;
}

.dad-joke-link {
  text-decoration: none;
}

.content-about-programming {
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
  padding: 0;
  display: flex;
  position: relative;
}

.content-about-language {
  flex-direction: column;
  width: 100%;
  padding: 5%;
  display: flex;
  position: relative;
}

.hobbies-activities {
  flex-direction: row;
  justify-content: space-around;
  display: flex;
}

.programming-card {
  text-align: center;
  z-index: 10;
  width: 300px;
  height: 300px;
  margin: 20px 20px 60px;
  transition: all .5s;
  position: relative;
}

.programming-card .box-about {
  z-index: 10;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.language-card {
  color: var(--text-color);
  flex-direction: row;
  align-items: flex-start;
  width: 100%;
  height: 50px;
  display: flex;
  position: relative;
}

.language-card-value {
  color: #000;
  border: solid 1px var(--navigation-color);
  background-color: #fff;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 100%;
  font-size: 20px;
  font-weight: 400;
  display: flex;
}

.language-card-lang {
  border: solid 1px var(--navigation-color);
  text-align: center;
  border-right: #111;
  justify-content: center;
  align-items: center;
  width: 20%;
  height: 100%;
  font-size: large;
  display: flex;
}

.percent-about {
  z-index: 100;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  position: relative;
}

.percent-about .number {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.percent-about .number h2 {
  color: var(--text-color);
  font-size: 50px;
  font-weight: 700;
}

.percent-about .number h2 span {
  font-size: 24px;
}

.text {
  text-align: center;
  z-index: 11;
  color: var(--text-color);
  text-transform: uppercase;
  letter-spacing: 1px;
  pointer-events: none;
  background: red;
  border-radius: 10px;
  padding: 10px 20px;
  font-size: 18px;
  font-weight: 800;
  position: absolute;
  bottom: -70px;
  left: 50%;
  transform: translateX(-50%)translateY(-50px);
  box-shadow: 0 15px 35px #0003;
}

svg {
  width: 150px;
  height: 150px;
  position: relative;
}

svg circle {
  fill: none;
  stroke-width: 10px;
  stroke: #ffffff0d;
  stroke-linecap: round;
  width: 100%;
  height: 100%;
  transform: translate(5px, 5px);
}

svg circle:nth-child(2) {
  stroke: var(--text-color);
  stroke-dasharray: 440;
  stroke-dashoffset: 440px;
}

.experiences {
  flex-direction: column;
  align-items: center;
  width: 100%;
  display: flex;
  position: relative;
}

.experiences:before {
  content: "";
  z-index: -1;
  background: #3f48cc;
  width: 250%;
  transition: all .5s;
  position: absolute;
  top: 0;
  bottom: 0;
  transform: none;
}

.experience-title {
  background-color: #00cdff;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  width: 200%;
  height: 60px;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
}

.about-titles {
  background-color: #00cdff;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  width: 200%;
  height: 60px;
  margin-top: 40px;
  margin-bottom: 20px;
  display: flex;
  position: relative;
  left: -50%;
}

.experiences-box {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 20px;
  display: flex;
}

.game-container * {
  width: 50%;
  margin: 25px 25%;
  font-size: 24px;
}

.game-container h1 {
  text-align: center;
  font-size: 90px;
}

.game-container img {
  border: 5px solid #000;
}

.game-container li {
  margin-left: 25px;
}

.game-download-button {
  height: 100px;
}

.game-download-button span {
  background-color: #ff4500;
  margin: 0;
}

.lingo-game-border {
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 5px 0;
  display: flex;
  position: relative;
}

.lingo-box {
  border: 10px solid var(--navigation-color);
  width: fit-content;
  height: 100%;
  display: grid;
  position: relative;
}

.lingo-row {
  visibility: hidden;
  background-color: #0000;
  border: none;
  position: absolute;
  top: 0;
}

.lingo-box span {
  border: solid var(--navigation-color);
  color: #000;
  text-overflow: bold;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  font-size: 60px;
  display: flex;
}

.lingo-box span div {
  opacity: 0;
  transition: all;
  transform: translateY(-50px);
}

.lingo-information {
  border: 10px solid var(--navigation-color);
  color: #000;
  text-align: center;
  background: linear-gradient(45deg, #1eff00, #00b7ff);
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 5px 0;
  padding: 5px;
  font-size: large;
  display: flex;
}

.lingo-message {
  border: 10px solid var(--navigation-color);
  color: #000;
  text-align: center;
  border-radius: 10px;
  margin: 5px 0;
}

.lingo-fieldset {
  text-align: left;
  background: var(--navigation-color-tertiary);
  color: #fff;
  border-radius: 10px;
  width: 60%;
  margin-top: 10px;
  padding: 10px;
}

.lingo-fieldset fieldset {
  border: none;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

#lingo-letters-show {
  opacity: 1;
  transition: all .1s ease-in;
  transform: translateY(0);
}

.lingo-mobile-input-row {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.lingo-mobile-row {
  justify-content: space-evenly;
  width: 100%;
  height: 100%;
  display: flex;
}

.lingo-mobile-input {
  color: #000;
  border: 1px solid var(--contact-color);
  width: 50px;
  height: 50px;
  margin: 2px;
}

.lingo-guess-hidden {
  opacity: 0;
  transition: all;
  transform: translateY(-10px);
}

.modal-close {
  float: right;
  opacity: .3;
  border: none;
  margin-top: -16px;
  margin-right: 32px;
  position: relative;
}

.modal-close:hover {
  opacity: 1;
}

.modal-close:before, .modal-close:after {
  content: " ";
  background-color: var(--navigation-color-secondary);
  width: 4px;
  height: 32px;
  position: absolute;
  left: 15px;
}

.modal-close:before {
  transform: rotate(45deg);
}

.modal-close:after {
  transform: rotate(-45deg);
}

.lingo-modal-content {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.lingo-share-button {
  border: none;
  height: 40px;
  margin-top: 40px;
}

.lingo-share-button span {
  background-color: var(--navigation-color-secondary);
}

.projects {
  flex-wrap: wrap;
  justify-content: space-around;
  display: flex;
}

.project-box {
  width: 400px;
  height: 400px;
  margin: 40px 30px;
  transition: all .5s;
  position: relative;
}

.project-box div {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.content-card {
  z-index: 1;
  color: var(--text-color);
  transition: all .5s;
  position: relative;
  left: 0;
}

.content-card h2, .content-card h4 {
  color: var(--text-color);
  text-align: start;
  margin-bottom: 10px;
  font-size: 2em;
}

.content-card p {
  color: #ffe5e5;
  margin-bottom: 30px;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.6;
  text-decoration: none;
}

.content-card img {
  box-sizing: border-box;
  background: #000;
  border: 2px solid #000;
  border-radius: 10px;
  height: 25%;
  padding: -1px;
  position: relative;
}

.content-card h4 {
  margin-bottom: 10px;
  font-size: 1.1em;
}

@media screen and (width <= 1536px) {
  .bottom-bar ul {
    width: 20%;
  }
}

@media screen and (width <= 1280px) {
  .bottom-bar ul {
    width: 25%;
  }

  .game-container * {
    width: 100%;
    margin: 25px 0;
  }
}

@media screen and (width <= 768px) {
  .bottom-bar ul {
    width: 30%;
  }

  .navigation-bar {
    height: 5vh;
    padding-bottom: 0;
  }

  .container {
    height: calc(100% - 11vh);
    padding: 5% 1% 0;
    top: 5vh;
  }

  .navigation-ul {
    background-color: var(--navigation-color-transparent);
    backdrop-filter: blur(15px);
    flex-direction: column-reverse;
    width: 0;
    height: 86vh;
    transition: transform .3s ease-in;
    position: absolute;
    top: 5vh;
    right: -1vh;
    transform: translateX(100%);
    box-shadow: 0 5px 15px #0000001a;
  }

  .navigation-ul li {
    width: 100%;
    height: 19%;
  }

  .burger {
    background: none;
    border: none;
    flex-direction: column;
    justify-content: center;
    width: 20%;
    height: 100%;
    display: flex;
  }

  .page-bar {
    z-index: 15;
    backdrop-filter: blur(15px);
    width: 0;
    height: calc(100% - 11vh);
    transition: all .3s;
    right: 0;
    transform: translateX(99%);
    box-shadow: 0 5px 15px #0000001a;
  }

  .user-navigation {
    width: 40%;
    right: calc(1vh + 60%);
  }

  .login-modal {
    width: 90%;
  }

  .nav-active-bar {
    width: 61%;
    transform: translateX(0%);
  }

  .nav-active {
    width: 60%;
    right: 0;
    transform: translateX(0%);
  }

  .nav-link-active {
    justify-content: center;
    align-items: center;
    display: flex;
  }
}

