:root {
    --navigation-color: rgb(23, 23, 23);
    --navigation-color-transparent: rgba(23, 23, 23, 0.8);
    --navigation-color-secondary: #da0037;
    --navigation-color-tertiary: rgb(23, 23, 23);
    --navigation-color-quaternary: rgb(237, 237, 237);
    --navigation-color-quaternary-transparent: rgba(237, 237, 237, 0.8);
    --navigation-color-quinary: aquamarine;
    --navigation-color-septary: rgba(127, 255, 212, 0.5);

    --text-color: #e2e2e2;
    --contact-color: #e26f2e;
    --contact-color-transparent: rgba(226, 111, 46, 0.7);
    --about-color: #3f48cc;
    --about-color-transparent: rgb(63, 72, 204, 0.7);
    --projects-color: #ea8aba;
    --projects-color-transparent: rgba(234, 138, 186, 0.7);
    --main-color: #193225;
    --main-color-transparent: rgba(25, 50, 37, 0.7);
}

* {
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
    padding: 0;
    margin: 0;
}

body {
    margin: 0;
    padding: 0;
    background: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)),
        url("Images/background/nature.jpg");
    background-size: 100% 100%;
    color: var(--text-color);
}

.react-root,
html,
body {
    height: 100%;
    overflow-x: hidden;
}

.glass-box {
    padding: 20px 40px;
    background: rgba(255, 255, 255, 0.05);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    border: 1px solid rgb(255, 255, 255, 0.1);
    border-radius: 20px;
    backdrop-filter: blur(15px);
}

.glass-box-container {
    height: 98%;
    width: 99%;
    margin: 0.5%;
    padding: 0;
}

.white-text {
    color: var(--text-color);
    font-size: larger;
}

.transparent-button {
    font-weight: bold;
    background-color: transparent;
    text-decoration: none;
    white-space: nowrap;
    border: 0;
    cursor: pointer;
}

/* Navigation Bar */
.navigation-bar {
    position: absolute;
    top: 0;
    background-color: var(--navigation-color-transparent);
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
    height: 6vh;
    width: 100%;
    z-index: 20;
    border: none;
    padding: 0 0 1vh 1vh;
    display: flex;
    align-items: stretch;
}

.navigation-logo {
    flex: 1;
}

.navigation-ul {
    width: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.outside-alerter {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
}

.fill-container {
    width: 100%;
    height: 100%;
    text-align: center;
    line-height: 5vh;
}

.navigation-container {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    width: 20%;
    height: 100%;
}

.user-navigation li:hover *,
.navigation-container:hover a {
    color: var(--navigation-color-secondary);
}

.user-navigation {
    position: absolute;
    top: 6vh;
    right: 1vh;
    width: 20%;
    background-color: var(--navigation-color-transparent);
    font-size: x-large;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: solid var(--navigation-color-secondary) 1px;
}

.user-navigation li {
    list-style: none;
}

.burger {
    display: none;
}

.burger div {
    width: 25px;
    height: 3px;
    background-color: var(--text-color);
    margin: 3px;
    transition: all 0.3s ease;
}

.burgerToggle .line1 {
    transform: rotate(-45deg) translate(-6.25px, 6.25px);
}

.burgerToggle .line2 {
    opacity: 0;
}

.burgerToggle .line3 {
    transform: rotate(45deg) translate(-6.25px, -6.25px);
}

.login-button {
    width: 60%;
    height: 60%;
}

.forgot-password-span {
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}
/* General CSS for every prgba(0, 0, 0, 0.774)age */
/* General button */

.general-button {
    display: inline-block;
    font-size: 1.1em;
    color: #111;
    background: var(--text-color);
    padding: 10px;
    border-radius: 4px;
    text-decoration: none;
    font-weight: 700;
    margin-top: 5px;
}

.general-button:hover {
    background-color: rgba(255, 255, 255, 0.5);
    cursor: pointer;
}

.button-blocked {
    background: grey;
    border: none;
}

.button-blocked:hover {
    background-color: red;
}
/* RGB button */
.rgb-button {
    position: relative;
    background: var(--navigation-color-transparent);
    border-radius: 50px;
    padding: 1px;
}

.rgb-button span {
    position: relative;
    background-color: var(--navigation-color-tertiary);
    z-index: 1;
    height: 100%;
    width: 100%;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.rgb-button::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
        45deg,
        #fb0094,
        #00f,
        #0f0,
        #ff0,
        #f00,
        #fb0094,
        #00f,
        #0f0,
        #ff0,
        #f00
    );
    opacity: 0;
    background-size: 400%;
    transition: 0.5s;
    filter: blur(4px);
    animation: animate_button 20s linear infinite;
    border-radius: 50px;
}

.rgb-button:hover::after {
    opacity: 1;
}

@keyframes animate_button {
    0% {
        background-position: 0 0;
    }

    50% {
        background-position: 300% 0;
    }

    100% {
        background-position: 0 0;
    }
}

/* Background color of a glass box */
.colored-background {
    content: "";
    position: absolute;
    z-index: -1;
    top: -5%;
    left: 30%;
    width: 40%;
    height: 110%;
    border-radius: 8px;
    transition: 0.5s;
    top: -5%;
    left: -5%;
    width: 110%;
    height: 110%;
    border-radius: 20px;
}

.background-glass-box .background-glass-box-child:nth-child(1),
.background-glass-box .background-glass-box-child:nth-child(2) {
    content: "";
    position: absolute;
    z-index: -1;
    top: -5%;
    left: 30%;
    width: 40%;
    height: 110%;
    border-radius: 8px;
    transform: skewX(15deg);
    transition: 0.5s;
}

.background-glass-box .background-glass-box-child:nth-child(2) {
    filter: blur(30px);
}

.background-glass-box:hover .background-glass-box-child:nth-child(1),
.background-glass-box:hover .background-glass-box-child:nth-child(2) {
    transform: skewX(0deg);
    top: -5%;
    left: -5%;
    width: 110%;
    height: 110%;
    border-radius: 20px;
}

/* Hovering span */

@keyframes animateProjects {
    0%,
    100% {
        transform: translateY(50px);
    }

    50% {
        transform: translatey(-50px);
    }
}

/* The full container*/
.container {
    position: relative;
    top: 6vh;
    width: 100%;
    height: 86vh;
    overflow-y: auto;
    overflow-x: hidden;
    text-align: justify;
    padding: 1% 10% 0% 10%;
}

.center-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.center-container h1 {
    font-size: 100px;
}

.center-container h2 {
    font-size: 40px;
}

.contact-form-submission-text {
    margin-top: 20px;
    text-align: center;
    font-weight: 1000;
}

.declined {
    background: rgba(255, 33, 33, 0.7);
    border: solid rgba(255, 33, 33, 0.7) 1px;
}

.loading {
    background: rgba(255, 246, 114, 0.7);
    border: solid rgba(255, 246, 114, 0.7) 1px;
}

.accepted {
    background: rgba(73, 255, 18, 0.7);
    border: solid rgba(73, 255, 18, 0.7) 1px;
}

/* Login Page */

.login-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.login-modal-background {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--navigation-color-transparent);
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-modal {
    width: 50%;
    height: fit-content;
    background-color: var(--navigation-color);
    border-radius: 50px;
    padding: 30px;
}

.login-modal-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

.login-container .login-box {
    position: relative;
    padding: 20px;
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--navigation-color-transparent);
    border-radius: 50px;
}

.login-box h2 {
    margin: 10px 0 10px 0;
}

.alternate-login-options {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 20%;
}

.btn-alternate-login {
    font-size: 18px;
    line-height: 1.2;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 15px;
    width: 40%;
    height: 70px;
    border-radius: 10px;
    transition: all 0.4s;
    position: relative;
    z-index: 1;
}

.btn-alternate-login::before {
    content: "";
    display: block;
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    background: linear-gradient(45deg, #1eff00, hsl(197, 100%, 50%));
    opacity: 0;
    transition: all 0.4s;
}

.btn-alternate-login {
    color: var(--navigation-color-tertiary);
    background-color: var(--text-color);
}

.btn-alternate-login img {
    width: 30px;
    margin-right: 15px;
    padding-bottom: 3px;
}

.login-form {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-decoration: none;
    flex-direction: column;
}

.login-form h1 {
    color: var(--text-color);
    margin-top: 10px;
}

.login-form input {
    border: 0;
    display: block;
    background: none;
    border: 2px solid var(--navigation-color-secondary);
    padding: 10px 6px;
    outline: none;
    color: var(--text-color);
    border-radius: 20px;
    transition: 0.25s;
    width: 100%;
    text-align: center;
}

.login-form-button {
    width: 80%;
    margin-top: 20px;
}

.create-account,
.forgot-password {
    margin-top: 2%;
    width: 100%;
    text-align: center;
    color: var(--text-color);
    font-size: 16px;
    text-decoration: underline;
    background-color: transparent;
    border: transparent;
    cursor: pointer;
}

.password-register-field {
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;
    width: 100%;
}

.password-register-span {
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    border-radius: 20px;
}

.password-strength-text {
    position: absolute;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    z-index: 30;
    right: 60px;
    top: 0;
    bottom: 0;
}

.up-arrow {
    border: solid 10px transparent;
    border-right-color: var(--navigation-color);
    position: absolute;
    left: 50%;
    top: -20px;
    transform: rotate(90deg);
}

.password-list-requirement {
    list-style: none;
}

.password-strength-hint-box {
    position: relative;
}

.password-strength-hint {
    position: absolute;
    font-size: large;
    background: var(--navigation-color);
    box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
    backdrop-filter: blur(100px);
    border-radius: 30px;
    padding: 20px 40px;
    z-index: 30;
    top: 75px;
    left: 150px;
}

.btn-alternate-login:hover:before {
    opacity: 1;
}

.hide-password-box {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
}

.hide-password-box-full {
    width: 100%;
}

.hide-password-icon {
    position: absolute;
    background: transparent;
    border: none;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 8px 0 0;
}

.hide-password-box img {
    height: 32px;
    width: 32px;
}
/* Page bar */
.page-bar {
    position: fixed;
    top: 5vh;
    width: 100%;
    height: 1vh;
    z-index: 21;
    background-color: var(--about-color);
}

/* Footer bar */
.bottom-bar {
    position: absolute;
    padding: 0;
    height: 6vh;
    width: 100%;
    background-color: var(--navigation-color-transparent);
    border-top-right-radius: 0;
    border-top-left-radius: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.bottom-bar ul {
    display: flex;
    height: 60%;
    width: 15%;
    transition: 0.5s;
}

.bottom-bar ul li {
    list-style: none;
    height: 100%;
    width: 25%;
}

.bottom-bar ul li a {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.bottom-bar ul li a img {
    height: 100%;
}

.footer-links:hover {
    transform: scale(0.9);
    filter: invert(1);
    border-radius: 50px;
}

/* Dashboard Page */
.list-lines {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: left;
}

.list-lines h1 {
    font-size: 8vh;
    margin-top: 20px;
}

.list-lines h2 {
    font-size: 3vh;
    margin-top: 20px;
}

/* Contact page */
.contact-section {
    display: flex;
    justify-content: center;
    align-items: center;
}

.form-container {
    height: auto;
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form-container textarea,
.form-container span,
.form-container input {
    width: 100%;
    border: none;
    font-size: 16px;
    letter-spacing: 1px;
    background: transparent;
}

.login-form input,
.form-container input {
    padding-top: 20px;
    padding-bottom: 20px;
}
.form-container h1 {
    font-size: 60px;
}

.form-container h1,
.form-container h2 {
    letter-spacing: 1px;
    margin-bottom: 10px;
    text-align: center;
}

.form-container textarea {
    height: 200px;
    letter-spacing: 1px;
    display: inline-block;
    resize: none;
}

.contact-form-padded {
    padding: 20px 20px;
    width: 100%;
}

.contact-form-submit-div {
    margin-top: 20px;
    width: 100%;
}

.contact-form-submit-div button {
    width: 100%;
    height: 100%;
}

.login-form input::placeholder,
.form-container textarea::placeholder,
.form-container input::placeholder {
    text-align: center;
    color: var(--navigation-color-quinary);
}

/* Preferences Page */

.preferences-container {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.preferences-container h1 {
    font-size: 60px;
}

.preferences-container h2 {
    font-size: 45px;
}

.preferences-container h3 {
    font-size: 30px;
}

.reset-password-full-width {
    width: 100%;
    padding: 0;
    text-align: center;
}

.preferences-yes-no {
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.preferences-separator-line {
    height: 10px;
    width: 200%;
    margin: 30px;
    background: linear-gradient(45deg, aqua, lime);
    border: none;
}

.preferences-yes-no button {
    width: 30%;
}
/* About page */
.container-about {
    padding-top: 5% 20% 5% 20%;
}

.about-information {
    margin-top: 5%;
    position: relative;
    width: 100%;
}

.about-information h2 {
    font-weight: 700;
    color: var(--text-color);
    font-size: 4em;
    line-height: 1.5em;
}

.about-information p {
    font-size: 24px;
    font-weight: 400;
    text-align: left;
}

.dad-joke-container {
    margin-top: 100px;
    margin-bottom: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.dad-joke-content {
    margin: 20px;
    text-align: justify;
    font-size: 20px;
}

.dad-joke-button {
    width: 80%;
    height: 40px;
    font-size: 30px;
    color: var(--text-color);
    text-align: center;
}

.dad-joke-link {
    text-decoration: none;
}

.content-about-programming {
    position: relative;
    padding: 0;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.content-about-language {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 5%;
}

.hobbies-activities {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.programming-card {
    position: relative;
    width: 300px;
    height: 300px;
    text-align: center;
    z-index: 10;
    margin: 20px 20px 60px;
    transition: 0.5s;
}

.programming-card .box-about {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    border-radius: 10px;
}

.language-card {
    position: relative;
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    width: 100%;
    height: 50px;
    color: var(--text-color);
}

.language-card-value {
    background-color: #ffffff;
    color: black;
    width: 80%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 400;
    border: solid 1px var(--navigation-color);
    font-size: 20px;
}

.language-card-lang {
    border: solid 1px var(--navigation-color);
    width: 20%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: large;
    height: 100%;
    border-right: #111;
}

.percent-about {
    position: relative;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    z-index: 100;
}

.percent-about .number {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.percent-about .number h2 {
    color: var(--text-color);
    font-weight: 700;
    font-size: 50px;
}

.percent-about .number h2 span {
    font-size: 24px;
}

.text {
    position: absolute;
    bottom: -70px;
    left: 50%;
    text-align: center;
    background: #f00;
    z-index: 11;
    color: var(--text-color);
    border-radius: 10px;
    font-weight: 800;
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 1px;
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
    pointer-events: none;
    transform: translateX(-50%) translateY(-50px);
    padding: 10px 20px 10px;
}

svg {
    position: relative;
    width: 150px;
    height: 150px;
}

svg circle {
    width: 100%;
    height: 100%;
    fill: none;
    stroke-width: 10;
    stroke: rgba(255, 255, 255, 0.05);
    transform: translate(5px, 5px);
    stroke-linecap: round;
}

svg circle:nth-child(2) {
    stroke: var(--text-color);
    stroke-dasharray: 440px;
    stroke-dashoffset: 440px;
}

.experiences {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.experiences::before {
    content: "";
    position: absolute;
    top: 0;
    width: 250%;
    background: #3f48cc;
    transform: none;
    z-index: -1;
    transition: 0.5s;
    bottom: 0;
}

.experience-title {
    background-color: rgb(0, 205, 255);
    margin-top: 20px;
    margin-bottom: 20px;
    width: 200%;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
}

.about-titles {
    background-color: rgb(0, 205, 255);
    margin-top: 40px;
    margin-bottom: 20px;
    width: 200%;
    position: relative;
    left: -50%;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
}

.experiences-box {
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

/* Games */

/* Rocket game page */
.game-container * {
    width: 50%;
    margin: 25px 25% 25px 25%;
    font-size: 24px;
}

.game-container h1 {
    font-size: 90px;
    text-align: center;
}

.game-container img {
    border: solid 5px black;
}

.game-container li {
    margin-left: 25px;
}

.game-download-button {
    height: 100px;
}

.game-download-button span {
    margin: 0;
    background-color: orangered;
}

/* Lingo Page */
.lingo-game-border {
    width: 100%;
    position: relative;
    border-radius: 5px;
    margin: 5px 0 5px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.lingo-box {
    position: relative;
    height: 100%;
    width: fit-content;
    display: grid;
    border: 10px solid var(--navigation-color);
}

.lingo-row {
    visibility: hidden;
    top: 0;
    position: absolute;
    background-color: transparent;
    border: none;
}

.lingo-box span {
    border: solid var(--navigation-color);
    height: 70px;
    width: 70px;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 60px;
    text-overflow: bold;
}

.lingo-box span div {
    opacity: 0;
    transform: translateY(-50px);
    transition: 0s;
}

.lingo-information {
    width: 100%;
    background: linear-gradient(45deg, #1eff00, hsl(197, 100%, 50%));
    border: 10px solid var(--navigation-color);
    border-radius: 10px;
    margin: 5px 0 5px 0px;
    padding: 5px;
    color: black;
    font-size: large;
    display: flex;
    align-items: center;
    text-align: center;
    flex-direction: column;
}

.lingo-message {
    border-radius: 10px;
    border: 10px solid var(--navigation-color);
    color: black;
    margin: 5px 0 5px 0px;
    text-align: center;
}

.lingo-fieldset {
    margin-top: 10px;
    padding: 10px;
    border-radius: 10px;
    width: 60%;
    text-align: left;
    background: var(--navigation-color-tertiary);
    color: white;
}

.lingo-fieldset fieldset {
    border: none;
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    align-items: center;
}

#lingo-letters-show {
    transition: all 0.1s ease-in;
    opacity: 1;
    transform: translateY(0px);
}

.lingo-mobile-input-row {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}

.lingo-mobile-row {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-evenly;
}

.lingo-mobile-input {
    height: 50px;
    width: 50px;
    margin: 2px;
    color: black;
    border: 1px solid var(--contact-color);
}

.lingo-guess-hidden {
    transform: translateY(-10px);
    opacity: 0;
    transition: 0s;
}
.modal-close {
    position: relative;
    float: right;
    margin-right: 32px;
    margin-top: -16px;
    border: none;
    opacity: 0.3;
}

.modal-close:hover {
    opacity: 1;
}

.modal-close:before,
.modal-close:after {
    position: absolute;
    left: 15px;
    content: " ";
    height: 32px;
    width: 4px;
    background-color: var(--navigation-color-secondary);
}

.modal-close:before {
    transform: rotate(45deg);
}

.modal-close:after {
    transform: rotate(-45deg);
}

.lingo-modal-content {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.lingo-share-button {
    margin-top: 40px;
    border: none;
    height: 40px;
}
.lingo-share-button span {
    background-color: var(--navigation-color-secondary);
}

/** Projects page */
.projects {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.project-box {
    position: relative;
    width: 400px;
    height: 400px;
    margin: 40px 30px;
    transition: 0.5s;
}

.project-box div {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.content-card {
    position: relative;
    left: 0;
    z-index: 1;
    transition: 0.5s;
    color: var(--text-color);
}

.content-card h2,
.content-card h4 {
    font-size: 2em;
    color: var(--text-color);
    margin-bottom: 10px;
    text-align: start;
}

.content-card p {
    font-size: 18px;
    line-height: 1.6;
    font-weight: 400;
    text-decoration: none;
    font-style: normal;
    color: #ffe5e5;
    margin-bottom: 30px;
}

.content-card img {
    position: relative;
    height: 25%;
    border-radius: 10px;
    padding: -1px;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    border: 2px solid black;
    background: black;
}

.content-card h4 {
    font-size: 1.1em;
    margin-bottom: 10px;
}

/* Other Screen Sizes */
@media screen and (max-width: 1536px) {
    .bottom-bar ul {
        width: 20%;
    }
}

@media screen and (max-width: 1280px) {
    .bottom-bar ul {
        width: 25%;
    }

    .game-container * {
        margin: 25px 0 25px 0;
        width: 100%;
    }
}

@media screen and (max-width: 768px) {
    .bottom-bar ul {
        width: 30%;
    }

    .navigation-bar {
        height: 5vh;
        padding-bottom: 0;
    }

    .container {
        top: 5vh;
        height: calc(100% - 11vh);
        padding: 5% 1% 0 1%;
    }

    .navigation-ul {
        position: absolute;
        right: -1vh;
        height: 86vh;
        width: 0;
        top: 5vh;
        background-color: var(--navigation-color-transparent);
        flex-direction: column-reverse;
        transform: translateX(100%);
        transition: transform 0.3s ease-in;
        box-shadow: 0 5px 15px rgb(0 0 0 / 10%);
        backdrop-filter: blur(15px);
    }

    .navigation-ul li {
        width: 100%;
        height: 19%;
    }

    .burger {
        display: flex;
        flex-direction: column;
        justify-content: center;
        background: transparent;
        border: none;
        width: 20%;
        height: 100%;
    }

    .page-bar {
        right: 0px;
        width: 0px;
        height: calc(100% - 11vh);
        transform: translateX(99%);
        transition: 0.3s;
        box-shadow: 0 5px 15px rgb(0 0 0 / 10%);
        z-index: 15;
        backdrop-filter: blur(15px);
    }

    .user-navigation {
        right: calc(1vh + 60%);
        width: 40%;
    }

    .login-modal {
        width: 90%;
    }

    /** All actives */
    .nav-active-bar {
        width: 61%;
        transform: translateX(0%);
    }

    .nav-active {
        right: 0;
        width: 60%;
        transform: translateX(0%);
    }

    .nav-link-active {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
